ight:15px;color:#DA690B;background:#f3f0f9}.search .search-option .search-bottom .search-row{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:flex-start;align-items:center;font-family:Roboto;font-style:normal;font-weight:300;font-size:13px;line-height:15px;color:#222222}.search .search-option .search-bottom .search-row .spacer{width:20px}.search .search-option .search-bottom .search-row input[type=text]{width:76px;padding:4px;margin-right:10px}.search .search-option .search-bottom .search-row .sort{cursor:pointer}.nav-header{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;height:100px}.nav-header .title{font-family:Nunito;font-style:normal;font-weight:300;font-size:30px;line-height:41px;text-transform:capitalize;color:#6848B5;flex:1 1 auto;margin-left:40px}.nav-header .need-help{width:120px;height:44px;background:#FF7200;border-radius:4px;font-family:Nunito;font-style:normal;font-weight:bold;font-size:12px;line-height:16px;text-transform:uppercase;color:#FFFFFF}.nav-bar{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;height:74px;background:-webkit-gradient(linear, left top, left bottom, from(rgba(63,0,215,0.6)), to(#6848B5));background:linear-gradient(180deg, rgba(63,0,215,0.6) 0%, #6848B5 100%);margin-bottom:20px;font-family:Nunito;font-style:normal;font-weight:normal;font-size:30px;line-height:30px;color:#FFFFFF}.nav-bar .nav-links{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;flex:0 1 1220px}.nav-bar .nav-links div{margin-right:30px;cursor:pointer}.nav-bar .nav-links div:hover{font-weight:bold}.nav-bar .nav-links .last-link{flex:1 1 auto}.nav-bar .nav-links .settings{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center;font-family:Nunito;font-style:normal;font-weight:300;font-size:20px;line-height:27px;color:#FFFFFF;margin-right:0}.page-titles{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:center;align-items:center}.page-titles .title{font-family:Nunito;font-style:normal;font-weight:bold;font-size:24px;line-height:33px;color:#4F4F4F;flex:1 1 auto}.page-titles .title .subtitle{height:41px;font-family:Roboto;font-style:normal;font-weight:normal;font-size:16px;line-height:19px;color:#000000}.footer{display:flex;flex-direction:column;flex-wrap:nowrap;justify-content:center;align-items:center;height:314px;background:#5C5C5C;box-shadow:0px 10px 20px rgba(31,32,65,0.05);font-family:Nunito;font-style:normal;font-weight:normal;font-size:14px;line-height:24px;color:rgba(255,255,255,0.75);margin-top:36px}.grid-row{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:flex-start;align-items:stretch;flex:1 1 auto;height:37px;color:black;font-family:Nunito, Arial, Helvetica, sans-serif}.grid-row.header{background-color:#7a57cf;color:white;border-top:1px solid #ddd}.grid-row.even{background-color:#f2f2f2}.grid-row.header:hover{background-color:#7a57cf}.grid-row:hover{background-color:#ddd}.grid-row>div{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:flex-start;align-items:center;padding-left:10px;border-bottom:1px solid #ddd;border-right:1px solid #ddd}.grid-row div:nth-of-type(1){flex:0 1 61px;border-left:1px solid #ddd}.grid-row div:nth-of-type(2){flex:1 1 364px}.grid-row div:nth-of-type(3){flex:1 1 355px}.grid-row div:nth-of-type(4){flex:1 1 138px}.grid-row div:nth-of-type(5){flex:0 1 195px}.grid-row div:nth-of-type(6){flex:0 1 94px}@media screen and (max-width: 600px){.flex-card{width:98vw;margin:10px}}

